import React, { useContext } from 'react';
import logo from './logo.svg';
import { Link } from 'react-router-dom';
import { useStateValue } from '../../context/BasketStateProvider';
import HeaderDropDownManu from './HeaderDropDownMenu';
import { CategoriesContext } from '../../context/categoriesContext';
import IconButton from '@material-ui/core/IconButton';
import AddShoppingCartIcon from '@material-ui/icons/AddShoppingCart';
import Grid from '@material-ui/core/Grid';
import { makeStyles } from '@material-ui/core/styles';
import Paper from '@material-ui/core/Paper';
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import InputBase from '@material-ui/core/InputBase';
import Typography from '@material-ui/core/Typography';
import SearchIcon from '@material-ui/icons/Search';
import MenuIcon from '@material-ui/icons/Menu';
import Badge from '@material-ui/core/Badge';

const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  appBar: {
    backgroundColor: ' #232f3e',
    paddingTop: 2,
    paddingBottom: 2,
  },
  menuButton: {
    flexBasis: 'auto',
    order: 1,
  },

  imgContainer: {
    flexBasis: 'auto',
    textAlign: 'center',
    order: 2,
  },
  img: {
    [theme.breakpoints.down('sm')]: {
      height: 40,
    },
  },
  searchContainer: {
    order: 4,
  },
  inputInput: {
    paddingTop: 4,
    paddingBottom: 4,
    paddingLeft: `calc(1em + ${theme.spacing(20)}px)`,
    backgroundColor: '#ffffff',
    color: '#232f3e',
    borderRadius: 5,
    fontSize: 20,
    [theme.breakpoints.down('lg')]: {
      marginTop: 10,
      marginBottom: 10,
    },
  },

  searchIcon: {
    right: 0,
    top: 10,
    bottom: 10,
    position: 'absolute',
    display: 'flex',
    alignItems: 'center',
    color: '#232f3e',
    zIndex: 10,
  },
  dropDown: {
    left: 0,
    height: '100%',
    position: 'absolute',
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    color: '#232f3e',
    zIndex: 10,
  },

  search: {
    position: 'relative',
  },
  navbarButtonsContainer: {
    order: 5,
  },
  navbarButtons: {
    textAlign: 'center',
    '&:hover': {
      cursor: 'pointer',
      color: '#ccc',
    },
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'row',
    },
  },
  navbarButton: {
    [theme.breakpoints.down('sm')]: {
      flexDirection: 'row',
      marginRight: 10,
      fontWeight: 'bold',
      fontSize: 14,
    },
  },
  cartBtnContainer: {
    order: 6,
    [theme.breakpoints.down('sm')]: {
      order: 3,
    },
  },
  cartBtn: {
    [theme.breakpoints.down('sm')]: {
      paddingBottom: 0,
    },
  },
}));

const Header = () => {
  const classes = useStyles();
  const DrawerContext = useContext(CategoriesContext);
  const { toggleLeftDrawer } = DrawerContext.leftDrawer;

  const [state, dispach] = useStateValue();

  const itemsNumber = state.basket.reduce((res, current) => {
    return res + current.quantity;
  }, 0);

  return (
    <div className={classes.root}>
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <Grid container alignItems="center">
            <Grid item xs={1} className={classes.menuButton}>
              <IconButton
                edge="start"
                color="inherit"
                aria-label="open drawer"
                onClick={toggleLeftDrawer(true)}
              >
                <MenuIcon />
              </IconButton>
            </Grid>

            <Grid item xs md={2} className={classes.imgContainer}>
              <Link to="/">
                <img alt="" className={classes.img} src={logo} />
              </Link>
            </Grid>
            <Grid item xs={12} md className={classes.searchContainer}>
              <div className={classes.search}>
                <div className={classes.searchIcon}>
                  <Paper
                    style={{
                      backgroundColor: '#febd69',
                      height: '100%',
                      width: 60,
                      display: 'flex',
                      justifyContent: 'center',
                      alignItems: 'center',
                    }}
                  >
                    <SearchIcon style={{ fontSize: 29 }} />
                  </Paper>
                </div>
                <div className={classes.dropDown}>
                  <HeaderDropDownManu className={classes.dropDown} />
                </div>
                <InputBase
                  fullWidth
                  placeholder="Search…"
                  className={classes.inputInput}
                  inputProps={{ 'aria-label': 'search' }}
                />
              </div>
            </Grid>
            <Grid
              item
              xs={12}
              md={3}
              container
              className={classes.navbarButtonsContainer}
            >
              {' '}
              <Grid
                item
                xs
                container
                direction="column"
                justify="center"
                className={classes.navbarButtons}
              >
                <Grid item>
                  <Typography
                    style={{ fontSize: 14 }}
                    className={classes.navbarButton}
                  >
                    Hello Guest
                  </Typography>
                </Grid>
                <Grid item>
                  <Link to="/signin">
                    <Typography
                      style={{ fontWeight: 'bold' }}
                      className={classes.navbarButton}
                    >
                      Sign In
                    </Typography>
                  </Link>
                </Grid>
              </Grid>
              <Grid
                item
                xs
                container
                direction="column"
                justify="center"
                className={classes.navbarButtons}
              >
                <Grid item>
                  <Typography
                    style={{ fontSize: 14 }}
                    className={classes.navbarButton}
                  >
                    Returns
                  </Typography>
                </Grid>
                <Grid item>
                  <Typography
                    style={{ fontWeight: 'bold' }}
                    className={classes.navbarButton}
                  >
                    &#38; Orders
                  </Typography>
                </Grid>
              </Grid>
            </Grid>
            <Grid
              item
              xs={1}
              container
              alignItems="center"
              justify="center"
              className={classes.cartBtnContainer}
            >
              <Link to="/checkout">
                <IconButton
                  aria-label="add to shopping cart"
                  className={classes.cartBtn}
                >
                  <Badge badgeContent={itemsNumber} color="secondary">
                    <AddShoppingCartIcon
                      fontSize="large"
                      style={{ color: '#FFFFFF' }}
                    />
                  </Badge>
                </IconButton>
              </Link>
            </Grid>
          </Grid>
        </Toolbar>
      </AppBar>
    </div>
  );
};

export default Header;
